'use strict';

/*
Usage <div data-alert-message data-messages="rootUi.msg" class="padding"></div>
*/
angular
  .module( _DIRECTIVES_ )
  .directive( 'alertMessage', function () {
    return {
      restrict: 'AE', //A = Attribute, C = Class Name, E = Element, M = HTML Comments
      templateUrl: 'public/views/common/alert-message.html',
      replace: true,
      scope: {
        messages: '='
      },
      link: function ( scope, iElement, iAttrs ) {
        scope.closeable = 'close' in iAttrs;
      }
    };
  } );
