'use strict';

angular.module( _SERVICES_ )
  .factory( 'AppDataSqlService', function ( $q, $ionicPlatform, ENV, SqLite, UtilService ) {

    var db = null;
    $ionicPlatform.ready( function () {
      db = SqLite.openDB( ENV.dbName );
    } );

    var getAppDataParsed = function ( id ) {
      var deferred = $q.defer();
      var query = 'SELECT * FROM appData WHERE id = ?';
      SqLite.execute( db, query, [ id ] )
        .then( function ( results ) {
          var appData;
          try {
            var resultAppData = results.rows.item( 0 );
            if ( resultAppData ) {
              var value = JSON.parse( resultAppData.value );
              appData = {};
              appData.id = resultAppData.id;
              appData.value = value;
            }
          } catch ( err ) {
            console.log( 'id ' + id + ' | catch err : ' + err.message );
          }
          deferred.resolve( appData );
        } );
      return deferred.promise;
    };
    return {
      saveAppData: function ( appData ) {
        var binding, query = 'DELETE FROM appData WHERE id = ?';
        SqLite.execute( db, query, [ appData.id ] );

        var value = JSON.stringify( appData.value );
        query = 'INSERT INTO appData (id, value, modifiedAt) VALUES (?, ?, ?)';
        binding = [ appData.id, value, UtilService.getTime() ];

        return SqLite.execute( db, query, binding );
      },
      getAppData: function ( id ) {
        return getAppDataParsed( id );
      }
    };
  } ); //end AppDataSqlService
