'use strict';

angular.module(_SERVICES_)
.factory('GlobalDataService', function($rootScope, $timeout) {

  var showSpinner = function(showSpinner){
    $rootScope.rootUi.showSpinner = showSpinner;
    $timeout(
      function () {
        $rootScope.rootUi.showSpinner = false;
      }, 30000 //max 30 sec.
    );
  };
  var working = function(working) {
    $rootScope.rootUi.working = working;
    $timeout(
      function () {
        $rootScope.rootUi.working = false;
      }, 5000 //max 5 sec. show working
    );
  };
  var addMsg = function(msg){
    $rootScope.rootUi.msg = msg;
    $timeout(
      function () {
        $rootScope.rootUi.msg = null;
      }, 4000 //max 4 sec. show msg
    );
  };
  /* JSON structure -
    "msg": [
    {
      "type": "success",
      "value": [
        {
          "title": "title 1",
          "text": "MSG-FORM-SUCCESS-SAVE",
          "param": null
        }
      . . .
  */
  var addMessage = function(type, text, title, param){
    var message = {};
    message.type = type;
    message.value = [];
    var messageVal = {};
    messageVal.text = text;
    messageVal.title = title;
    messageVal.param = param;
    message.value.push(messageVal);
    var msg = [];
    msg.push(message);
    addMsg(msg);
  };
  return {
    showSpinner: function(status){
      showSpinner(status);
    },
    working : function() {
      working(true);
    },
    isWorking : function() {
      return $rootScope.rootUi.working; //return $rootScope.rootUi.working not working holding old state //TODO verify one more time
    },
    resetWorking : function() {
      $rootScope.rootUi.working = false;
    },
    addMessage: function(type, text, title, param){
      addMessage(type, text, title, param);
    },
    addMsg : function(msg){
      addMsg(msg);
    }
  };//end return
});//end GlobalDataService
