/* global goog */
'use strict';

angular.module( _CONTROLLERS_ )
  .controller( 'MainController', function ( $rootScope, $ionicPlatform, $timeout, ENV, MainService ) {

    /* ************************************ Private function/object ************************************ */
    var socket = {};

    /*
    ActivityStatusController - reloadSiteActivities - EVENT.RELOAD_ACTIVITY_STATUS
    CarStatusController - reloadCustomerActivities - QueueDetail - EVENT.RELOAD_CAR_STATUS
    SiteStatusController - reloadCustomerVisitedSites
    */
    var broadcast = function ( event ) {
      $rootScope.$broadcast( event );
    };
    /* ************************************ socket function/object ************************************ */
    var initSocket = function () {
      socket.onopen = function () {
        console.log( 'Channel Connected!' );
      };
      socket.onmessage = function ( message ) {
        if ( message.data ) {
          var jsonData = JSON.parse( message.data );
          if ( jsonData.event ) {
            broadcast( jsonData.event );
          }
        }
      };
      socket.onerror = function () {
        console.log( 'Connection Error!' );
      };
      socket.onclose = function () {
        console.log( 'Connection Closed!' );
        openChannel();
      };
    };
    var openChannel = function () {
      MainService.getToken()
        .then( function ( response ) {
          if ( response && response.data.status === 'SUCCESS' ) {
            //VAL.channelToken = response.data.data.token;
            var token = response.data.data.token;
            var channel = new goog.appengine.Channel( token );
            socket = channel.open();
            initSocket();
          }
        } );
    };
    var init = function () {
      if ( ENV.openChannel ) {
        $timeout( function () {
          openChannel();
        }, 1000 );
      }
    };

    /* ************************************ Public function/object ************************************ */
    $ionicPlatform.ready( function () {
      init();
    } );

  } );
