/* global ionic */
'use strict';

/* see jshint error http://jslinterrors.com/redefinition-of-a/ */
/*jshint -W079 */
var _APP_ = 'com.common.mobile.core.App',
  _CONTROLLERS_ = _APP_ + '.controllers',
  _DIRECTIVES_ = _APP_ + '.directives',
  _FILTERS_ = _APP_ + '.filters',
  _MODULES_ = _APP_ + '.modules',
  _SERVICES_ = _APP_ + '.services',
  _CONFIG_ = _APP_ + '.config',
  _SQLITE_ = _APP_ + '.sqlite';

/* INFO
Declare app level module which depends on filters, services, etc
top-level module
angular.module is a global place for creating, registering and retrieving Angular modules
'com.common.mobile.core.App' is the name of this angular module example (also set in a <body> attribute in index.html)
the 2nd parameter is an array of 'requires'
*/
var customerApp = angular.module( _APP_, [
  // Your application's namespaced modules, so they won't conflict with other modules.
  // You shouldn't have to touch these unless you want to.
  _CONTROLLERS_,
  _DIRECTIVES_,
  _FILTERS_,
  _MODULES_,
  _SERVICES_,
  _CONFIG_,
  _SQLITE_,

  'ionic',
  'angular.filter', //@see https://github.com/a8m/angular-filter
  'auto.generated', //@see /app/public/scripts/config/generated.js   //REMOVE
  'ngCordova',
  'ionic.service.core', //serves as the backbone for all of the ionic.io services. It allows our app to register Ionic Users and communicate with Ionic's servers.
  'ionic.service.push', //is the core Ionic Push code. It communicates with the push notification plugin we installed before, and allows us to register devices to receive push notifications.
  'ionic.service.analytics',
  'pascalprecht.translate', //for i18n
  'mgo-angular-wizard', //Angular-Wizard
  //'ja.qr',//janantala angular-qr
  // 'angulartics'//use angulartics
  'angulartics.google.analytics.cordova' //angulartics for GA
] );

// Create global modules. You shouldn't have to touch these.
angular.module( _CONTROLLERS_, [] );
angular.module( _DIRECTIVES_, [] );
angular.module( _FILTERS_, [] );
angular.module( _MODULES_, [] );
angular.module( _SERVICES_, [] );
angular.module( _CONFIG_, [] );
angular.module( _SQLITE_, [] );


var getOSVersion = function ( ENV ) {
  var os;
  if ( ionic.Platform.isIOS() ) {
    os = ENV.os.ios;
  } else if ( ionic.Platform.isAndroid() ) {
    os = ENV.os.android;
  } else if ( ionic.Platform.isWindowsPhone() ) {
    os = ENV.os.win;
  } else {
    os = ENV.os.android;
  }
  return os;
};

/* INFO
document.addEventListener("deviceready", function () {
  $cordovaPlugin.someFunction().then(success, error);
}, false);
OR with IONIC $ionicPlatform An angular abstraction of ionic.Platform
$ionicPlatform.ready(function() {
*/
customerApp.run( function ( $rootScope, $ionicPlatform, $ionicHistory, $state, $ionicCoreSettings,
  $cordovaDevice, $cordovaAppVersion, $cordovaNetwork,
  ENV, VAL, VALEXT, EVENT,
  SqlService, SetupService, NetworkStatusService, NotificationService, AnalyticService, FileService ) {

  $rootScope.rootUi = angular.copy( ENV.emptyRootUi );

  $ionicPlatform.ready( function () {
    // kick off the platform web client
    // Ionic.io(); http://docs.ionic.io/v1.0/docs/user-quick-start

    VAL.isDevice = ( window.cordova !== undefined ); // && ( ionic.Platform.isIOS() || ionic.Platform.isAndroid() || ionic.Platform.isWindowsPhone() );

    VAL.device.appId = $ionicCoreSettings.get( 'app_id' );

    //if(window.cordova) or if(window.device)
    if ( VAL.isDevice ) {
      NetworkStatusService.alertOfflineNetworkStatus();

      $cordovaAppVersion.getVersionNumber()
        .then( function ( version ) {
          VAL.device.verName = version; //0.0.1
        } );
      $cordovaAppVersion.getVersionCode()
        .then( function ( build ) {
          VAL.device.verCode = build; //18
        } );

      VAL.device.id = $cordovaDevice.getUUID(); //-OR- device.uuid;
      VAL.device.os = getOSVersion( ENV ); //-OR- $cordovaDevice.getPlatform()
      VAL.device.osVer = $cordovaDevice.getVersion();
      //$cordovaDevice.getModel() //TODO add all to analytics

      NotificationService.initialize(); //setupPushRegister

      AnalyticService.initialize();

    } //end isDevice

    // Hide the accessory bar by default (remove this to show the accessory bar above the keyboard
    // for form inputs).
    // The reason we default this to hidden is that native apps don't usually show an accessory bar, at
    // least on iOS. It's a dead giveaway that an app is using a Web View. However, it's sometimes
    // useful especially with forms, though we would prefer giving the user a little more room
    // to interact with the app.
    if ( window.cordova && window.cordova.plugins.Keyboard ) {
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar( true );
      cordova.plugins.Keyboard.disableScroll( true );
    }
    if ( window.StatusBar ) {
      // Set the statusbar to use the default style, tweak this to
      // remove the status bar on iOS or change it to use white instead of dark colors.
      StatusBar.styleDefault();
    }

    /* init db / create schema / create table if not exist */
    SqlService.onCreate()
      .then( function () {
        console.log( 'all tables created by now', VAL );
        FileService.createDir(); //FIXME PATH_EXISTS_ERR
        SetupService.beforeAll();
      } );

    $rootScope.$on( EVENT.RELOAD_APP, function () {
      console.log( 'RELOAD_APP called' );
      SetupService.beforeAll();
    } );

    $rootScope.$on( '$stateChangeStart', function ( event, toState, toParams, fromState, fromParams ) {
      try {
        AnalyticService.trackView( toState.name );
      } catch ( err ) {
        console.log( 'in $stateChangeStart', err );
        console.log( 'rootScope stateChangeStart event ' + event + ' toState ' + JSON.stringify( toState ) + ' toParams ' + JSON.stringify( toParams ) + ' fromState ' + JSON.stringify( fromState ) + ' fromParams ' + JSON.stringify( fromParams ) );
      }
      //console.log('rootScope stateChangeStart event '+ event +' toState '+JSON.stringify(toState) +' toParams '+JSON.stringify(toParams) +' fromState '+JSON.stringify(fromState) +' fromParams '+JSON.stringify(fromParams));
      var state = toState.name;
      if ( state && !( state.indexOf( 'blank' ) > -1 || state.indexOf( 'welcome' ) > -1 || state.indexOf( 'first-launch' ) > -1 || state.indexOf( 'sign-in' ) > -1 || state.indexOf( 'sign-out' ) > -1 ) ) {
        //if ( state && !state.includes( 'blank' ) && !state.includes( 'first-launch' ) && !state.includes( 'sign-in' ) && !state.includes( 'sign-out' ) ) {
        //console.log('rootScope stateChangeStart event '+ event +' toState '+JSON.stringify(toState) +' toParams '+JSON.stringify(toParams) +' fromState '+JSON.stringify(fromState) +' fromParams '+JSON.stringify(fromParams));
        SetupService.updateUiState( state );
      }
    } );

  } ); //end ionicPlatform.ready


  /*
    $ionicPlatform.onHardwareBackButton(function () {
      $ionicHistory.goBack();
    });
  document.addEventListener('backbutton', function(event){
    event.preventDefault(); // EDIT
    navigator.app.exitApp(); // exit the app
  });
  see http://codepen.io/calendee/pen/eqbkl?editors=101
  $ionicViewService.getBackView is deprecated, please use $ionicHistory.backView instead: http://ionicframework.com/docs/nightly/api/service/$ionicHistory/ vendor.7869339b.js:2

  */
  $ionicPlatform.registerBackButtonAction( function () {
    //if($state.is('app.main-page')) {
    //            ionic.Platform.exitApp();
    //      }
    //$ionicHistory.goBack(); //FIXME check see if it work

    console.log( '$rootScope.$viewHistory ', $rootScope.$viewHistory );
    console.log( '$ionicHistory.backView()', $ionicHistory.backView() );
    console.log( '$ionicHistory currentStateName', $ionicHistory.currentStateName() );
    if ( $ionicHistory.currentStateName() === VALEXT.state.landing ) {
      //ionic.Platform.exitApp();
      navigator.app.exitApp();
      //ionic.Platform.exitApp(); // stops the app
      // or do nothing
    } else {
      //var backView = $ionicHistory.backView();
      //console.log('backView', backView);
      // if( ! backView) {
      //   console.log('backView.go');
      //   backView.go();
      // }
      //$state.go($ionicHistory.backView());
      //$ionicHistory.goBack();
      $state.go( VALEXT.state.landing );
    }
  }, 100 );

} ); //end App.run
