'use strict';

/*
 * config/i18n.js
 *
 * Defines the i18n for the application.
 * 
 http://userguide.icu-project.org/locale
 http://www.lingoes.net/en/translator/langcode.htm
 https://msdn.microsoft.com/en-us/library/ms533052(v=vs.85).aspx
 https://msdn.microsoft.com/en-us/library/ee825488(v=cs.20).aspx

 de-de
 en-us, es-us, en-au, and zh-cn
 hi-in

Spanish(Spain)-Español, es_ES. Spanish(USA), es_US. Spanish(Uruguay)

 english - en
 deutsch - de
 Country and Language
 <select name="select_locale" class="intl_drop">
				<option value="0">- Select One -</option>
				<option value="1">United States - English</option>
				<option value="2">United States - Español</option>
				<option value="3">Mexico - Español</option>
				<option value="4">Canada - English</option>
				<option value="5">Canada - Français</option>
			</select>

 */
angular.module(_APP_)
.config(function ($translateProvider) {

  $translateProvider.useStaticFilesLoader({
    prefix: 'public/i18n/',
    suffix: '.json'
  });
  $translateProvider.preferredLanguage('en-us');

});
