/* global ionic */
'use strict';

angular.module( _CONTROLLERS_ )
  .controller( 'RateShareController', function ( $scope, $ionicPlatform, $window, VALEXT ) {

    /* ************************************ Private function/object ************************************ */
    var emptyUi = {
      isAndroid: false
    };

    var reset = function () {
      $scope.ui = angular.copy( emptyUi );
      $scope.ui.msg = VALEXT.share.msg;
      $scope.ui.website = VALEXT.share.website;
      $scope.ui.isAndroid = ionic.Platform.isAndroid(); //(VAL.os === ENV.os.android);
    };
    var initRateShare = function () {
      reset();
    };

    //Working @see http://stackoverflow.com/questions/4191492/launch-facebook-app-from-other-app
    var checkFacebookApp = function ( url1, url2, callback ) {
      var schemeOrPackage = $scope.ui.isAndroid ? 'com.facebook.katana' : 'fb://';
      console.log( 'schemeOrPackage ', schemeOrPackage );
      $window.appAvailability.check( schemeOrPackage, function () {
        console.log( 'appAvailability yes' );
        callback( url1 );
      }, function () {
        console.log( 'appAvailability No' );
        /*
        // facebook is not installed,
        // then open play store to download and install facebook
        window.open('market://details?id=com.facebook.katana', '_system', 'location=no');
        */
        callback( url2 );
      } );
    };
    var openUrl = function ( url ) {
      console.log( 'final url ', url );
      $window.open( url, '_system', 'location=no' );
    };
    /*
  @see http://www.gajotres.net/how-to-launch-external-application-with-ionic-framework/2/
  @see https://github.com/kevendra/com.lampa.startapp
  var check = function(){
    var scheme;
    // Don't forget to add the org.apache.cordova.device plugin!
    if(device.platform === 'iOS') {
      scheme = 'twitter://';
    }else if(device.platform === 'Android') {
      scheme = 'com.twitter.android';
    }
    appAvailability.check(
      scheme, // URI Scheme
      function() {  // Success callback
        window.open('twitter://user?screen_name=gajotres', '_system', 'location=no');
        console.log('Twitter is available');
      },
      function() {  // Error callback
        window.open('https://twitter.com/gajotres', '_system', 'location=no');
        console.log('Twitter is not available');
      }
    );
  };
*/
    $ionicPlatform.ready( function () {
      initRateShare();
    } );

    /* ************************************ Public function/object ************************************ */
    $scope.openWeb = function () {
      openUrl( VALEXT.share.website );
    };
    $scope.openAppStore = function () {
      openUrl( VALEXT.share.appStoreUrl );
    };
    $scope.openPlayStore = function () {
      var url = 'market://details?id=' + VALEXT.share.appPackageName;
      openUrl( url );
    };
    $scope.openFacebook = function () { //Working @see http://stackoverflow.com/questions/4191492/launch-facebook-app-from-other-app
      if ( $scope.ui.isAndroid ) {
        var url1 = 'fb://page/' + VALEXT.share.fbPageId; // fb://page/870549469709982
        var url2 = VALEXT.share.fbPageUrl; //'https://www.facebook.com/bellme.xyz';//'https://www.facebook.com/pages/BellMe/870549469709982';
        checkFacebookApp( url1, url2, function ( url ) {
          openUrl( url );
        } );
      } else {
        //checkFacebookApp not working in iOS
        var url = 'fb://profile/' + VALEXT.share.fbPageId; // 'fb://profile/870549469709982'
        openUrl( url ); //page will be open in facebook app
      }
    };


  } );
