'use strict';

angular.module( _SERVICES_ )
  .factory( 'AnalyticService', function ( $q, $cordovaGoogleAnalytics, $ionicAnalytics, ENV, VAL ) {
    var initializationIonicAnalytics = function () {
      // Add this inside your $ionicPlatform.ready function that is defined inside the run function:
      $ionicAnalytics.register();
    };
    var initializationGoogleAnalytics = function () {
      console.log( 'initialization - Google Analytics Plugin :' );
      //initialization - Google Analytics Plugin
      if ( VAL.isDevice ) { //typeof analytics add module //'angulartics.google.analytics.cordova'
        $cordovaGoogleAnalytics.setUserId( VAL.device.id ); //create unique session to track
        $cordovaGoogleAnalytics.startTrackerWithId( ENV.gaTrackerId );
      } else {
        //it won't exist for platforms other than iOS and Android
        console.log( 'Google Analytics Unavailable' );
      }
    };

    return {
      initialize: function () {
        var deferred = $q.defer();
        if ( ENV.analyticOnIonic ) {
          initializationIonicAnalytics();
        }
        if ( ENV.analyticOnGoogle ) {
          initializationGoogleAnalytics();
        }
        deferred.resolve( 'all done!' );
        return deferred.promise; //promise
      },
      trackView: function ( stateName ) {
        if ( VAL.isDevice ) {
          if ( ENV.analyticOnIonic ) {
            $ionicAnalytics.track( stateName );
          }
          if ( ENV.analyticOnGoogle ) {
            $cordovaGoogleAnalytics.trackView( stateName );
          }
        }
      }
    }; //end return
  } ); //end
