/* global ionic */
'use strict';

/*
 * config/router.js
 *
 * Defines the routes for the application.
 *
 *  ngmin will take care of
 *  .config(['$routeProvider', function($routeProvider) {
 *  .config(function ($routeProvider) {
 */

angular.module( _APP_ )
  .config( function ( $ionicConfigProvider, $stateProvider, $urlRouterProvider ) { //, datepickerConfig, datepickerPopupConfig

    /*
    To enable native scrolls you can either:
    a) Add overflow-scroll=true to the content tag.
    This is a bit tedious approach and will get enabled for every platform (remember about iOS behavious).
    b) As a second approach, we can enable native scrolls for Android only devices
    and do it in one place, in the app's main file:
    */
    // Enable native scrolls for Android platform only,
    // as you see, we're disabling jsScrolling to achieve this.
    //see http://ionicframework.com/docs/api/service/$ionicPlatform/
    //see http://blog.ionic.io/native-scrolling-in-ionic-a-tale-in-rhyme/
    if ( ionic.Platform.isAndroid() ) {
      //console.log('jsScrolling false ');
      //$ionicConfigProvider.scrolling.jsScrolling(false);
      //$ionicConfigProvider.platform.android.scrolling.jsScrolling(false);
    }

    // if none of the above states are matched, use this as the fallback
    //$urlRouterProvider.otherwise('/');
    $urlRouterProvider.otherwise( '/blank' );

    $stateProvider
    /*
    An abstract state can have child states but can not get activated itself.
    An 'abstract' state is simply a state that can't be transitioned to.
    It is activated implicitly when one of its descendants are activated.
    */
      .state( 'blank', {
        url: '/blank',
        templateUrl: 'public/views/common/blank.html'
      } )
      .state( 'welcome', {
        url: '/welcome',
        templateUrl: 'public/views/common/welcome.html'
      } )

    //
    // .state( 'app.first-launch', {
    //   cache: false,
    //   url: '/first-launch',
    //   views: {
    //     'menuContent': {
    //       templateUrl: 'public/views/common/first-launch.html'
    //     }
    //   }
    // } )

    ;

  } );
