/*global BASE */
'use strict';

angular.module( _APP_ )
  .config( function ( $httpProvider, $provide ) {

    var isServerCall = function ( config ) {
      //config.url.indexOf('api/') = 0 if found and !config.url.indexOf('api/') = true
      return ( config && config.url && ( !config.url.indexOf( BASE ) ) ); // search for /api/ or api/
    };
    /*
        $provide.factory( 'apiInterceptor', [ 'VAL', function ( VAL ) {
          return {
            request: function ( config ) {
              if ( isServerCall( config ) ) {
                //ONE time $http.defaults.headers.common.Authorization = 'Bearer ' + VAL.token;
                config.headers.authorization = 'Bearer ' + VAL.token; //Authorization: Bearer <token>
                config.headers[ 'x-access-token' ] = VAL.token;
              }
              return config;
            }
          }; //end return
        } ] ); //end apiInterceptor
    */
    //showSpinner
    //  Global spinner handling
    $provide.factory( 'spinnerInterceptor', [ '$q', 'GlobalDataService', function ( $q, GlobalDataService ) {
      return {
        request: function ( config ) {
          if ( isServerCall( config ) ) {
            GlobalDataService.showSpinner( true );
          }
          return config;
        },
        response: function ( response ) {
          if ( response && isServerCall( response.config ) ) {
            GlobalDataService.showSpinner( false );
          }
          return response || $q.when( response ); //has to return a response object or a promise.
        },
        responseError: function () {
          GlobalDataService.showSpinner( false );
        }
      }; //end return
    } ] ); //end alertMessageInterceptor

    //  Global message handling
    $provide.factory( 'alertMessageInterceptor', [ '$q', 'GlobalDataService', function ( $q, GlobalDataService ) {
      return {
        response: function ( response ) {
          //do something on success
          //only receive responses that fall below status code 300, there is no use for it when we want to catch 401.
          //Will only be called for HTTP up to 300
          if ( !String.prototype.startsWith ) {
            String.prototype.startsWith = function ( str ) {
              return !this.indexOf( str );
            };
          }
          if ( response && ( ( response.config.url )
              .startsWith( BASE ) ) ) { //examine JSON response
            GlobalDataService.addMsg( response.data.msg );
          }
          //Global http error handling
          return response || $q.when( response ); //has to return a response object or a promise.
        }
      }; //end return
    } ] ); //end alertMessageInterceptor

    // Handling Failed Authentication from the API
    $provide.factory( 'signsOutUserOn401', [ '$q', '$location', '$rootScope', 'VALEXT', function ( $q, $location, $rootScope, VALEXT ) {
      return {
        //response rejection
        responseError: function ( rejection ) {
            // Not loged in or user session has expired, please re-login.
            if ( rejection.status === 401 ) { //401 Unauthorized
              //401 response and possibly some sort of authentication exception
              //location.reload();
              console.log( '401 Unauthorized. Must authenticate first.' );
              //SessionService.unset('authData');
              //AuthenticationService.unauthorizedAccess(); //FIXME Circular Dependency $injector:cdep
              $location.path( VALEXT.path.signIn );
              //$state.go( VALEXT.state.signIn );
            } else if ( rejection.status === 403 ) { //403 Forbidden wrong username or password
              console.log( '403 Forbidden wrong username or password or Access Denied' );
              //AuthenticationService.signinFailure();  //FIXME Circular Dependency $injector:cdep
              //SessionService.unset('authData');
              $rootScope.user.showSignInFailureMessage = true; //FIXME
              $location.path( VALEXT.path.signIn );
              //$state.go( VALEXT.state.signIn );
            } else if ( rejection.status >= 400 && rejection.status < 500 ) {
              //ErrorService.setError('Server was unable to find what you were looking for... Sorry!!');
            }

            // Session has expired - sessionRecoverer
            if ( rejection.status === 419 ) {

            }
            // otherwise, default behaviour
            return $q.reject( rejection );
          } //end responseError
      }; //end return
    } ] ); //end signsOutUserOn401

    /*
    TODO move below line to config.js
    Origin:http://localhost:9000
    Referer:http://localhost:9000/

    Access-Control-Allow-Credentials:true
    Access-Control-Allow-Methods:POST, OPTIONS, GET, PUT, DELETE
    Access-Control-Allow-Origin:http://localhost:9000
    Access-Control-Max-Age:3600

    $httpProvider.defaults.headers.common['Access-Control-Allow-Origin'] = '*';

    Server side
    Access-Control-Allow-Headers: Content-Type
    Access-Control-Allow-Methods: GET, POST, OPTIONS
    Access-Control-Allow-Origin: *
    */
    $httpProvider.defaults.headers.common[ 'Access-Control-Allow-Origin' ] = '*';
    delete $httpProvider.defaults.headers.common[ 'X-Requested-With' ]; //@see CorsFilter.java
    //$httpProvider.interceptors.push( 'apiInterceptor' );
    $httpProvider.interceptors.push( 'signsOutUserOn401' );
    $httpProvider.interceptors.push( 'alertMessageInterceptor' );
    $httpProvider.interceptors.push( 'spinnerInterceptor' );

  } );
