'use strict';

angular.module(_SERVICES_)
.factory('AssetService', function($http, $q, $cordovaFile, $cordovaFileTransfer, API, VAL, AssetSqlService) {

/*
GET /api/profile/asset-upload-url.json
POST /api/profile/asset-upload
DELETE /api/profile/asset-delete.json?blob-key= blobKey
GET /api/asset/asset-serve?blob-key=

ASSET_UPLOAD_HANDLER_PROFILE:   PROFILE  + 'asset-upload-url.json',
ASSET_UPLOAD                :   PROFILE  + 'asset-upload',
ASSET_DELETE                :   PROFILE  + 'asset-delete.json',
ASSET_SERVE                 :   ASSET  + 'asset-serve',
*/
  return {

    assetUploadUrl: function(){
      return $http.get(API.ASSET_UPLOAD_HANDLER_PROFILE);
    },
    assetDelete: function(blobKey){
      var config = {params : {'blob-key': blobKey}};
      return $http.delete(API.ASSET_DELETE, config);
    },
    assetServe: function(blobKey){
      var config = {params : {'blob-key': blobKey}};
      return $http.get(API.ASSET_SERVE, config);
    },
    // assetUpload: function(blobKey){
    //   var config = {params : {'blobKey': blobKey}};
    //   return $http.delete(API.ASSET_UPLOAD);
    // },

    /*
      dir - image audio video
    */
    assetUrl: function(blobKey, dir){
      var deferred = $q.defer();

      if(blobKey){

        var targetFilePath;
        var dirPath = cordova.file.externalDataDirectory + dir +'/';
        AssetSqlService.getAsset().then(function(asset){//SELECT * FROM asset
          console.log('asset :', asset);

          if(asset && VAL.isDevice){
            console.log('asset id and fileName ', asset.id, asset.fileName);

            //check file exist
             $cordovaFile.checkFile(dirPath, asset.fileName)
              .then(function(){//success
                targetFilePath = dirPath + asset.fileName;
                deferred.resolve(targetFilePath);
              }, function(){//error
                //FIXME TODO or download if online
                deferred.reject(undefined);
              });
          }else{
            var fileName = (new Date()).getTime();

            var downloadUrl = API.ASSET_SERVE + '?blob-key=' + blobKey;// ASSET_SERVE + "?" + BLOB_KEY + "=" + blobKey;
            targetFilePath = dirPath + fileName;
            var options = {};
            var trustHosts = true;

            console.log('targetFilePath ', targetFilePath);
            //download
            $cordovaFileTransfer.download(downloadUrl, targetFilePath, options, trustHosts)
              .then(function(result) {
                // Success!
                console.log('Success ', result);
                //save
                var saveAsset = {};
                saveAsset.id = blobKey;
                saveAsset.fileName = fileName;
                AssetSqlService.saveAsset(saveAsset);

                deferred.resolve(targetFilePath);
              }, function(err) {
                // Error
                console.log('Error ', err);
                deferred.reject(undefined);
              }, function(){//progress) {
                // $timeout(function () {
                //   $scope.downloadProgress = (progress.loaded / progress.total) * 100;
                // });
              });
          }
        });//end getAsset

      }else{
        console.log('blobKey is null', blobKey);
        deferred.reject(undefined);
      }

      return deferred.promise;//promise
    }

  };//end return
});//end AssetService
