'use strict';
/*
Note: Make sure that you never overwrite the value service/object as a whole otherwise your assignment is lost.
Always reassign the property values of the value object.
The following assignment is wrong and does not lead to the expected behavior
  user = response.data.user;

even if the layout of the response matches the user value object exactly.
We have to do a mapping property by property, i.e
  user.fullname = response.data.user.fullname;
  user.role = response.data.user.role;
  //etc.
*/
angular.module( _SERVICES_ )
  .value( 'VAL', {
    device: {
      id: '0', //must be string device.uuid deviceId like 52548590-9962-4277-9C37-95C8A8A27B7C or c6da166c0838ef8e
      //uid: 0, //server generated Id //need to store in SQLite
      type: 'PH',
      os: 'ANDROID',
      osVer: '',
      appId: '', //fixed size like ionic provide to each app settings.get('app_id') -OR- me.parakh.edu
      verCode: '', //appBuild: 0, VersionCode(Android) Version(iOS) //build identifier of the app
      verName: '' //appVersion: 0, VersionName(Android) Build(iOS) //version number of the current app running
    },
    claim: null, //user info
    token: null,
    firstLaunch: false,
    isDevice: false,
    data: {} //share data between controller
  } );
