'use strict';
/*
 Usage: <div
 data-prk-photo
 data-blob-key="model.person.blobKey"
 data-placeholder-class="placeholder-photo"></div>

 placeholder-photo
 placeholder-logo
 */
angular
.module(_DIRECTIVES_)
.directive('prkPhoto', function (AssetService) {

  var getPhotoUrl = function(scope){
    AssetService.assetUrl(scope.blobKey, 'image').then(function(photoUrl){
      if(photoUrl){
        scope.photoUrl = photoUrl;
      }else{
        scope.photoUrl = undefined;
      }
    }, function() {
      scope.photoUrl = undefined;
    });
  };
  return {
    restrict:'A', //A = Attribute, C = Class Name, E = Element, M = HTML Comments
    scope:{
      blobKey: '=',
      placeholderClass: '@'
    },
    template: '<img data-ng-class="{ \'{{placeholderClass}}\' : !photoUrl}" data-ng-src="{{photoUrl}}"/>',
    link: function (scope) {
      scope.$watch('blobKey', function(){
        getPhotoUrl(scope);
      });
      if(scope.blobKey){
        getPhotoUrl(scope);
      }
    }
  };
});
