'use strict';

angular.module( _SERVICES_ )
  .factory( 'RegistrationService', function ( $http, ENV, VAL, API ) {

    return {
      deviceRegistration: function () {
        var deviceModel = angular.copy( VAL.device );
        if ( VAL.claim ) {
          deviceModel.uid = VAL.claim.uid;
        }
        return $http.post( API.DEVICE, deviceModel );
      },
      pushNotificationRegistration: function ( token ) {
        var tokenModel = {};
        tokenModel.id = VAL.device.id + ENV.SEPARATOR + VAL.device.appId;
        //tokenModel.os = VAL.device.os;
        tokenModel.token = token;
        tokenModel.active = true;
        return $http.post( API.PUSH_NOTIFICATION, tokenModel );
      }
    }; //end return
  } );
