'use strict';


angular.module(_SERVICES_)
.factory('SqLite', function ($q) {

  return  {

    //@see https://github.com/CodeYellowBV/deviceReady/blob/master/deviceReady.js
    openDB: function (dbName) {//init
      var db;

      //FIXME check deviceReady
      var isCordova = typeof window.cordova !== 'undefined';

      var dbRootObject = isCordova ? window.sqlitePlugin : window;

      if (typeof dbRootObject.openDatabase === 'undefined') {
        window.alert('Your browser has no SQL support!  Please try a Webkit-based browser');
        return null;
      }
      //if (window.sqlitePlugin !== undefined) {
      if(isCordova){//SqLite
        //db = window.sqlitePlugin.openDatabase({name: dbName});//in production
        db = dbRootObject.openDatabase({name: dbName, location: 'default'});//in production
      }else{//WebSql - For debugging in simulator fallback to native SQL Lite
        console.log('WebSql init');
        var DEFAULT_SIZE = 5000000; // max to avoid popup in safari/ios
        //db = window.openDatabase(dbName, '1.0', 'DemoDB', DEFAULT_SIZE);
        db = dbRootObject.openDatabase(dbName, '1.0', 'DemoDB', DEFAULT_SIZE);
      }
      // ...Some more convenience and specific schema creation code...

      return db;
    },
    openDBBackground: function (dbName) {//NOT-IN-USE
      return window.sqlitePlugin.openDatabase({name: dbName, bgType: 1});
    },
    execute: function (db, query, binding) {
      var q = $q.defer();

      db.transaction(function (tx) {
        tx.executeSql(query, binding, function (tx, result) {
          q.resolve(result);
        },
        function (transaction, error) {
          q.reject(error);
        });
      });
      return q.promise;
    },
    nestedExecute: function (db, query1, query2, binding1, binding2) {//NOT-IN-USE
      var q = $q.defer();

      db.transaction(function (tx) {
        tx.executeSql(query1, binding1, function (tx, result) {
          q.resolve(result);
          tx.executeSql(query2, binding2, function (tx, res) {
            q.resolve(res);
          });
        },
        function (transaction, error) {
          q.reject(error);
        });
      });
      return q.promise;
    }

  };//end return
});//end SqLite


