'use strict';

angular.module(_SERVICES_)
.factory('MediaService', function($cordovaMedia, ENV, VAL) {

  var mediaUrl = 'public/media/samsung-whistle.mp3';
  var mediaUrlOnline = 'http://web.bellme.xyz/' + mediaUrl;
  var media;

  var iOSPlayOptions = {
    numberOfLoops: 1,
    playAudioWhenScreenIsLocked : true
  };

  var getMediaURL = function(uri) {
    if(VAL.os === ENV.os.android){//OR if(device.platform.toLowerCase() === "android"){
      return '/android_asset/www/' + uri;
    }
    return uri;
  };
  var mediaError = function(e) {
    console.log('Media Error', JSON.stringify(e));
  };
  var play = function(media) {
    media.stop();//media play only once, second play need to stop first
    if(VAL.os === ENV.os.ios){
      media.play(iOSPlayOptions); // iOS only!
    }else if(VAL.os === ENV.os.android){
      //$cordovaMedia.play(media);
      media.play(); // Android
    }
  };

  /* ************************************ Public function/object ************************************ */

  return {
    play : function() {
      var src = getMediaURL(mediaUrl);//MP3 file that is shipped with application
      media = $cordovaMedia.newMedia(src, null, mediaError);
      play(media);
    },
    playOnline : function() {
      media = $cordovaMedia.newMedia(mediaUrlOnline, null, mediaError);
      play(media);
    },
    pause : function() {
      media.pause();
    },
    stop : function() {
      media.stop();
    },
    release : function() {
      media.release();
    }
  };//end return
});//end
