'use strict';

angular.module( _CONTROLLERS_ )
  .controller( 'AuthenticationController', function ( $rootScope, $scope, $state, ENV, VAL, VALEXT, EVENT, AuthenticationService, SetupService, UtilService, AppDataService ) {

    /* ************************************ Private function/object ************************************ */

    var emptyModel = {
      username: null,
      password: null
    };

    var init = function () {
      $scope.model = angular.copy( emptyModel );
    };

    /* ************************************ Public function/object ************************************ */
    init();

    $scope.signin = function () {
      AuthenticationService.signin( $scope.model )
        .then( function ( response ) {

          var token = response.headers( ENV.tokenHeaderName );
          console.log( 'x-access-token: ' + token );

          var appData = {};
          appData.id = ENV.appDataTypeTOKEN;
          appData.value = token;
          AppDataService.saveAppDataLocal( appData )
            .then( function () {

              AppDataService.getSTAppData()
                .then( function ( settingAppData ) {
                  var val = {};
                  val.theme = settingAppData.value.theme;
                  val.lang = settingAppData.value.lang;
                  val.uiState = settingAppData.value.uiState;

                  //update app data and then launch beforeAll
                  var appDataDto = ENV.emptyAppDataDto;
                  //appDataDto.id = VAL.claim.uid + ENV.SEPARATOR + ENV.appDataTypeST; //server will take care
                  appDataDto.uid = VAL.claim.uid;
                  appDataDto.type = ENV.appDataTypeST;
                  appDataDto.val = val;

                  AppDataService.saveAppData( appDataDto )
                    .then( function () {
                      SetupService.beforeAll();
                    } );
                } );
            } );
        }, function () {
          $state.go( VALEXT.state.signIn );
        } );
    };
    $scope.signout = function () {
      AuthenticationService.signout()
        .then( function () {
          $rootScope.$broadcast( EVENT.ONLINE_STATE_CHANGE );
          $state.go( VALEXT.state.signIn );
        } );
    };
    $scope.resetPassword = function () {
      AuthenticationService.resetPassword( $scope.model.username )
        .then( function ( response ) {
          if ( response.data.staus === 'SUCCESS' ) {
            // var message = '';//
            // sendSms( $scope.model.username, message );
            $state.go( VALEXT.state.signIn );
          }
        } );
    };


  } );
