'use strict';

angular.module( _SERVICES_ )
  .factory( 'SqlService', function ( $q, $ionicPlatform, ENV, SqLite ) {

    var db = null;

    $ionicPlatform.ready( function () {
      db = SqLite.openDB( ENV.dbName );
    } );

    return {
      onCreate: function () {
        console.log( 'onCreate' );
        // creating required tables
        /*
          id - generated
          user
          title
          text
          status - 0(in-complete false) , 1(completed true)
          modifiedAt
         */
        var query = 'CREATE TABLE IF NOT EXISTS todo (id INTEGER primary key, user INTEGER, title TEXT, text TEXT, status INTEGER, modifiedAt NUMERIC)';
        var todoPromise = SqLite.execute( db, query );
        /*
        @see OverrideModelDataImpl#getAdditionalData or Api.SHARE_DATA
        id - SHARE_DATA
        value -
          sessionCurrent
          sessionPrevious
          allSession
          allSessionNotCurrent
        */
        query = 'CREATE TABLE IF NOT EXISTS appData (id TEXT primary key, value TEXT, modifiedAt NUMERIC)';
        var appDataPromise = SqLite.execute( db, query );
        //or use file system
        /*
        id - studentClassEnrollmentId
        fileName - IMG-<getTime()>  AUD-<getTime()>  VID-<getTime()>  send
        modifiedAt
        */
        query = 'CREATE TABLE IF NOT EXISTS asset (id TEXT primary key, fileName TEXT, modifiedAt NUMERIC)';
        var assetPromise = SqLite.execute( db, query );
        //SqLite.execute(db, query);
        var promises = [ todoPromise, appDataPromise, assetPromise ];
        return $q.all( promises );
      },
      dropAll: function () {
        /*
        While it is true that there is no DROP ALL TABLES command you can use the following set of commands.

        Note: These commands have the potential to corrupt your database, so make sure you have a backup
        */
        var query = 'PRAGMA writable_schema = 1';
        SqLite.execute( db, query );

        query = 'delete from sqlite_master where type in (\'table\', \'index\', \'trigger\')';

        console.log( 'query ' + query );
        SqLite.execute( db, query );

        query = 'PRAGMA writable_schema = 0';
        SqLite.execute( db, query );

        query = 'VACUUM'; //recover the deleted space with
        SqLite.execute( db, query );

        query = 'PRAGMA INTEGRITY_CHECK'; //good test to make sure everything is ok
        SqLite.execute( db, query );
      },
      onUpgrade: function () {
        // on upgrade drop older tables

        // create new tables
        //onCreate();
      }
    };
  } ); //end SqlService
