'use strict';

angular.module( _SERVICES_ )
  .factory( 'AppDataService', function ( $q, $http, ENV, VAL, API, AppDataSqlService ) {

    var getEmptyIfSTAppDataNull = function ( settingAppData ) {
      if ( !settingAppData ) {
        //create settingAppData
        settingAppData = {};
        settingAppData.id = ENV.appDataTypeST;
        settingAppData.value = ENV.emptySTAppData;
      }
      return settingAppData;
    };
    var getAppData = function ( id ) {
      return AppDataSqlService.getAppData( id );
    };
    return {
      saveAppData: function ( appData ) {
        return $http.post( API.SETTING, appData );
      },
      saveAppDataLocal: function ( appData ) {
        return AppDataSqlService.saveAppData( appData );
      },
      getSTAppData: function () {
        var deferred = $q.defer();
        getAppData( ENV.appDataTypeST )
          .then( function ( settingAppData ) {
            deferred.resolve( getEmptyIfSTAppDataNull( settingAppData ) );
          } );
        return deferred.promise;
      },
      getAppData: function ( id ) { //see authCheck auth-service
        return getAppData( id );
      }
    }; //end return
  } );
