'use strict';

angular.module( _CONTROLLERS_ )
  .controller( 'VersionController', function ( $scope, $ionicPlatform, VAL ) {

    /* ************************************ Private function/object ************************************ */
    var emptyUi = {
      version: undefined,
      versionCode: undefined
    };

    var reset = function () {
      $scope.ui = angular.copy( emptyUi );
    };
    var initVersion = function () {
      reset();
      $scope.ui.version = VAL.device.verName;
      $scope.ui.versionCode = VAL.device.verCode;
    };

    $ionicPlatform.ready( function () {
      initVersion();
    } );

    /* ************************************ Public function/object ************************************ */

  } );
