'use strict';

angular.module( _SERVICES_ )
  .factory( 'SetupService', function ( $http, $rootScope, $q, $state, $cordovaNetwork, $timeout,
    $ionicHistory, EVENT, ENV, VAL, VALEXT, UtilService,
    AppDataService, AppDataExtService, AppUpdateService, RegistrationService ) {

    var goToLastUiState = function () {
      var deferred = $q.defer();
      AppDataService.getSTAppData()
        .then( function ( settingAppData ) {
          if ( settingAppData.value.uiState ) {
            $state.go( VALEXT.state.landing ) //just to build history
              .then( function () { //FIXME app.about-us with parent state
                var backView = $ionicHistory.viewHistory()
                  .currentView;
                $ionicHistory.backView( backView ); //(VALEXT.state.landing);
                $state.go( settingAppData.value.uiState ); //FIXME $ionicHistory
              } );
            // console.log('XXXXX $ionicHistory ', $ionicHistory);
            // console.log('XXXXX $ionicHistory ', $ionicHistory.viewHistory());
          } else {
            $state.go( VALEXT.state.landing );
          }
          $rootScope.$broadcast( EVENT.APP_LAUNCH );
          deferred.resolve( 'all done!' );
        } );
      return deferred.promise; //promise
    };
    var updateNavigation = function ( navigation ) {
      var deferred = $q.defer();
      AppDataService.getSTAppData()
        .then( function ( settingAppData ) {
          settingAppData.value.navigation = navigation;
          deferred.resolve( AppDataService.saveAppDataLocal( settingAppData ) );
        } );
      return deferred.promise; //promise
    };

    var getTokenAndUpdateVAL = function () {
      var deferred = $q.defer();
      AppDataService.getAppData( ENV.appDataTypeTOKEN )
        .then( function ( appData ) {
          if ( appData ) {
            var token = appData.value;
            UtilService.updateVALAndHeaders( token );
          }
          deferred.resolve( 'all done!' );
        } );
      return deferred.promise; //promise
    };
    /*
    data.person:
    data.cars: or data.sites:
    //setting
    data.navigation:
    data.lang
    data.token
    data.register:
    */
    var firstLaunchForRegisteredUser = function ( data ) {
      var deferred = $q.defer();
      var promises = [];
      var index = 0;

      promises[ index++ ] = updateNavigation( data.navigation );

      //start registration
      //NotificationService.setupPushRegister();

      AppDataService.getSTAppData()
        .then( function ( settingAppData ) {
          settingAppData.value.navigation = data.navigation;
          settingAppData.value.lang = data.lang;
          settingAppData.value.theme = data.theme;
          settingAppData.value.activeSiteId = data.activeSiteId;
          settingAppData.value.activeAreaId = data.activeAreaId;
          /*
          var isReg = settingAppData.value.notification.register;
          var localNotificationRegister = (null !== isReg && isReg);
          settingAppData.value.notification.token = data.token;
          settingAppData.value.notification.register = data.register;
          if(data.register && ! localNotificationRegister){
            NotificationService.pushRegister();//if server says it registered (true) and local db say no(flase)
            //settingAppData.value.register = true;
            //this process will take time so register = true and token will set after saveAppData
          }
          */
          promises[ index++ ] = AppDataService.saveAppDataLocal( settingAppData );

        } );

      promises[ index++ ] = AppDataExtService.saveCustomSetting( data );

      $q.all( promises )
        .then( function () {
          $rootScope.$broadcast( EVENT.APP_LAUNCH );
          deferred.resolve( 'all done!' );
        } );
      return deferred.promise; //promise
    };
    var firstLaunch = function () {
      VAL.firstLaunch = true;
      var deferred = $q.defer();
      $timeout( function () {
        console.log( 'in state go app.first-launch' );
        $state.go( VALEXT.state.welcome );
        deferred.resolve( 'all done!' );
      }, 1000 );

      return deferred.promise; //promise
    };

    return {
      updateUiState: function ( uiState ) {
        var deferred = $q.defer();
        try {
          AppDataService.getSTAppData()
            .then( function ( settingAppData ) { //SELECT * FROM appData where id = 'SETTING'
              settingAppData.value.uiState = uiState;
              deferred.resolve( AppDataService.saveAppDataLocal( settingAppData ) );
            } );
        } catch ( e ) {
          deferred.resolve( e );
        }
        return deferred.promise; //promise
      },
      beforeAll: function () {
        var deferred = $q.defer();
        AppUpdateService.applyUpdatesOnVersionChanged()
          .then( function () {
            getTokenAndUpdateVAL()
              .then( function () {
                if ( VAL.isDevice && $cordovaNetwork.isOffline() ) {
                  //offline app launch
                  deferred.resolve( goToLastUiState() );
                } else {
                  RegistrationService.deviceRegistration()
                    .then( function success( response ) { //FromServer
                      if ( response ) {
                        //online app launch
                        var data = response.data.data;
                        if ( !data ) {
                          data = {};
                        }
                        firstLaunchForRegisteredUser( data )
                          .then( function () {
                            if ( data.appData ) { //we can use data.usr if needed change server
                              console.log( 'normalFlow ' );
                              //usr profile found
                              deferred.resolve( goToLastUiState() );
                            } else {
                              console.log( 'firstLaunch ' );
                              //usr profile NOT found
                              deferred.resolve( firstLaunch( data ) );
                            }
                          } );
                      } else {
                        deferred.resolve( goToLastUiState() );
                      } //end if response.data
                    }, function error( err ) {
                      console.log( 'beforeAll deviceRegistration error - offline app launch', err );
                      deferred.resolve( goToLastUiState() );
                    } );
                } //end else online

              } ); //updateTokenToVAL
          } ); //end applyUpdatesOnVersionChanged

        return deferred.promise; //promise
      }

    }; //end return
  } ); //end
