/* global cordova */
'use strict';

angular.module(_SERVICES_)
.factory('FileService', function($q, $cordovaFile, VAL) {

  var createDir = function(){

    var replace = false;//do not replace if it's there
    /*
      IMG-  AUD-  VID-  send
      FileError {code: 12, message: "PATH_EXISTS_ERR"}
    */
    if(VAL.isDevice){
      var image = $cordovaFile.createDir(cordova.file.externalDataDirectory, 'image', replace);
      var audio = $cordovaFile.createDir(cordova.file.externalDataDirectory, 'audio', replace);
      var video = $cordovaFile.createDir(cordova.file.externalDataDirectory, 'video', replace);
      var promises = [image, audio, video];//videoPromise
      return $q.all(promises);
    }
    var deferred = $q.defer();
    deferred.resolve('all done!');
    return deferred.promise;//promise
  };

  return {
    createDir: function(){
      return createDir();//promise
    }
  };//end return
});//end
