'use strict';

angular.module(_SERVICES_)
.factory('MainService', function($http, $q, ENV, VAL, API) {

  return {
    getToken : function() {
      var deferred = $q.defer();
      var isPartnerApp = ENV.appName === 'PARTNER';
      var config = {params : {'deviceId': VAL.uuid, 'isPartnerApp': isPartnerApp}};
      deferred.resolve($http.get(API.CHANNEL_TOKEN, config));
      return deferred.promise;
    }

  };//end return
});//end MainService
