'use strict';

angular.module( _SERVICES_ )
  .factory( 'UtilService', function ( $http, VAL ) {

    var getUserClaimData = function () {
      var claim = {};
      var token = VAL.token;
      if ( token ) {
        var payload = token.split( '.' );
        //$base64.decode issue does not able to decode -  $base64.decode('eyJleHAiOjE0NjgyNTUzMzczMTIsInVpZCI6NTEwNTY1MDk2MzA1NDU5Mn0');
        //claim = JSON.parse( $base64.decode( payload[ 1 ] ) );
        claim = JSON.parse( window.atob( payload[ 1 ] ) );
      }
      return claim;
    };
    var updateVALAndHeaders = function ( token ) {
      VAL.token = token;
      $http.defaults.headers.common.Authorization = 'Bearer ' + token;
      var claim = getUserClaimData();
      VAL.claim = claim;
      //VAL.device.uid = claim.uid; //TODO refactor and remove this use - VAL.claim.uid
    };

    return {
      isNotNull: function ( value ) {
        return ( undefined !== value && null !== value ); //typeof value !== undefined && value;
      },
      /*
      add space in between text html
      */
      addSpaceInBetweenText: function ( value, spaceAfter ) {
        var retValue = '';
        for ( var i = 0; i < value.length; i++ ) {
          if ( i % spaceAfter === 0 ) {
            retValue += ' ';
          }
          retValue += value.charAt( i );
        }
        return retValue;
      },

      //edu
      emptyDeleteEntityAppData: function () {
        var deleteEntityAppData = {};
        deleteEntityAppData.id = 'DELETE_ENTITY';
        deleteEntityAppData.value = {}; //kind: ids:[] X or kind:[] is easy to search
        return deleteEntityAppData;
      },
      isEmptyObjectNew: function ( obj ) {
        return Object.keys( obj )
          .length === 0; //ECMAScript 5 support
      },
      isEmptyObject: function ( obj ) { //return JSON.stringify(obj) === '{}';
        for ( var prop in obj ) {
          if ( obj.hasOwnProperty( prop ) ) {
            return false;
          }
        }
        return true;
      },
      sum: function ( a, b ) {
        if ( a && b ) {
          return parseInt( a ) + parseInt( b );
        }
        if ( a ) {
          return parseInt( a );
        }
        if ( b ) {
          return parseInt( b );
        }
        return;
      }, //end sum
      /*
      array without duplicates
      function ArrNoDupe(a) {
          var temp = {};
          for (var i = 0; i < a.length; i++){
              temp[a[i]] = true;
          }
          var r = [];
          for(var k in temp){
            r.push(k);
          }
          return r;
      }
      */
      unique: function ( a ) {
        var arr = [];
        for ( var i = 0; i < a.length; i++ ) {
          if ( arr.indexOf( a[ i ] ) === -1 ) {
            arr.push( a[ i ] );
          }
        }
        return arr;
      },
      getTime: function () {
        return new Date()
          .getTime();
      },
      updateVALAndHeaders: function ( token ) {
        updateVALAndHeaders( token );
      }
    }; //end return
  } ); //end
