'use strict';

angular.module( _SERVICES_ )
  .factory( 'AuthenticationService', function ( $http, $sanitize, ENV, VAL, API, UtilService ) {

    var sanitizeCredentials = function ( credentials ) {
      return 'username=' + $sanitize( credentials.username ) + '&password=' + $sanitize( credentials.password ) + '&deviceId=' + VAL.device.id + '&deviceType=' + VAL.device.type;
    };

    return {
      signin: function ( credentials ) {

        var config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        };
        var signin = $http.post( API.SIGNIN, sanitizeCredentials( credentials ), config );

        signin.then( function ( response ) {
          var token = response.headers( ENV.tokenHeaderName );
          console.log( 'x-access-token: ' + token );
          UtilService.updateVALAndHeaders( token );
        } );

        return signin;
      },
      signout: function () {
        var signout = $http.get( API.SIGNOUT );
        signout.then( function () {
          var token; // = undefined;
          UtilService.updateVALAndHeaders( token );
          console.log( 'signout.success' );
        } );
        return signout;
      }

    }; //end return
  } );
